}

body {
  background-color: #ebebeb;
  font-family: Times New Roman, Times, serif !important;
}

.bread {
  width: 100%;
  height: 100%;
  position: relative;
}

.bread:after {
  content: "";
  opacity: .1;
  background-image: url("bg.d5c8c458.png");
  background-repeat: repeat-x;
  background-size: 65px 55px;
  position: absolute;
  inset: 0;
}

.search-button {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 10em;
  padding: 1em 2em;
  font-size: 17px;
  font-weight: 600;
  transition: all .3s ease-in-out;
  box-shadow: 0 0 #000;
  color: #000 !important;
  background-color: #ebebeb !important;
}

.search-button:hover {
  transform: translateY(-4px)translateX(-2px);
  box-shadow: 2px 5px #8c6c48;
}

.search-button:active {
  transform: translateY(2px)translateX(1px);
  box-shadow: 0 0 #8c6c48;
}

body img {
  width: 100px;
  height: 500px;
}

