  border: none;
  border-radius: 4px;
  outline: none;
  padding: 15px;
  font-size: 18px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#myBtn:hover {
  background-color: #555;
}

.accept-btn {
  color: #fff !important;
  background-color: #d8b172 !important;
